<style lang="less" scoped>
.submitedWork{
  padding-top: 20px;
  background-color: #fff;
  min-height: 300px;
}
.work_it{
  width: 100%;
  display: flex;
  margin-top: 20px;
  &>div:nth-child(1){
    flex: 3;
    padding: 30px 20px;
    box-sizing: border-box;
    text-align: center;
  }
  &>div:nth-child(2){
    flex: 7;
    &>div{
      height: 200px;
      overflow: hidden;
      display: inline-block;
      &>img{
        width: 200px;
        height: 200px;
      }
      &>p{
        width: 200px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        margin-top: 5px;
      }
      &>p{
          font-size: 25px;
      }
      &>p:nth-child(2),&>p:nth-child(4){
          color: #888;
      }
    }
  }
}
.large_font{
  font-size: 50px;
  font-weight: bold;
  display: inline-block;
  margin:  20px 0 0 0;
}
.month{
  font-size: 30px;
}
.noWrapper{
  text-align: center;
  transform: scale(0.6);
  display: block;
  color: #777;
  font-size: 40px;
}
.right-content{
  & > div:nth-child(1){
    width: 200px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }
}
</style>

<template>
  <div class="submitedWork">
    <p class="noWrapper" v-if="!submitedWork || !submitedWork.length"><img :src="noWrapper" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
    <div class="work_it" v-for="(item, index) in submitedWork" :key="index" @click="enterWorkDetail(item)">
      <div>
        <h3 class="large_font">{{item.jobcompletionE.submit | ddFm}}</h3><span class="month"> {{item.jobcompletionE.submit | mmFm}}月</span>
      </div>
      <div class="right-content">
        <div :style="{ backgroundImage: 'url('+ item.jobcompletionE.contentImgSmall.split(',')[0] +')' }"></div>
        <div>
          <p>{{item.taskE.title}}</p>
          <p>{{item.taskE.expectName}}</p>
          <p>{{item.jobcompletionE.content}}</p>
          <p>共{{item.imgCount}}张</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      id: '',
      noWrapper: require('../../assets/img/da.png'),
      submitedWork: [],
      page: 1,
      pagination: true,
      falgNoData: false,
    }
  },
  mounted () {
    this.$Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
    })
    this.id = this.$route.params.id
    this.axios.post('/api/jobsList/submited', {
      page: this.page,
      id: this.id
    }).then(data => {
      this.submitedWork = data.data.list
      this.$Indicator.close()
      window.onscroll = () => {
        this.dropDown()
      }
    }).catch(err => {
      console.log(err)
    });
  },
  methods: {
    enterWorkDetail (item) {
      this.$router.push({
        name: 'workRangeDetail',
        params:{
          taskId: item.taskE.taskId,
          jobcompletionId: item.jobcompletionE.jobcompletionId
        }
      })
    },
    dropDown() {
      if (this.falgNoData) { //数据全部加载完了
        return
      } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
        this.falgNoData = true

        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        
        this.axios.post('/api/jobsList/submited', {
          id: this.id,
          page: ++this.page
        }).then(res => {
          if (this.pagination) {
            if (res.data.list && res.data.list.length > 0) {
              this.falgNoData = false
              this.$Indicator.close()
              this.submitedWork = this.submitedWork.concat(res.data.list)
            } else {
              this.$Indicator.close()
              this.$Toast({
                message: '没有更多了',
                position: 'middle',
                duration: 1000
              })
              this.pagination = false
              this.falgNoData = true
            }
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
  }
}
</script>


